'use strict';

var titleCase = require('to-title-case');
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var CountryActions = require('../actions/countryActions');
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var CountryStore = require('../stores/countryStore');
var FilteredProjectsStore = require('../stores/filteredProjectsStore');
var TopoLayer = require('./map/topoLayer.jsx');
var PointLayer = require('./map/pointLayer.jsx');
var Popup = require('./map/popup.jsx');
var Filters = require('./filters.jsx');
var Header = require('./header.jsx');
var NotYet = require('./notYet.jsx');


var Country = React.createClass({displayName: "Country",

  mixins: [
    Reflux.connect(CountryStore, 'countries'),
    Reflux.connect(FilteredProjectsStore, 'projects'),
  ],

  getInitialState: function() {
    return {filtersWidth: 225};
  },

  componentWillMount: function() {
    // make sure we load the country data. http cache should make this cheap.
    CountryActions.loadCountry.triggerPromise(this.props.params.countryId)
      .then(this.countryDidLoad);
  },

  componentWillReceiveProps: function(nextProps) {
    if (nextProps.params.countryId !== this.props.params.countryId) {
      // user navigated from one country directly to another's url
      delete this.country;
      CountryActions.loadCountry.triggerPromise(nextProps.params.countryId)
        .then(this.countryDidLoad);
    }
  },

  countryDidLoad: function(raw) {
    // for now, work with the raw country data.
    // we can't to countryStore.getCountry, because this callback runs before
    // countryStore.onLoadCountryCompleted.
    // we should maybe have another store for the current country...
    this.country = CountryStore.utils.countryToFeature(raw);
    MetaActions.setTitle(this.country.properties.name);
    MapViewActions.changeBounds(this.country.properties.bounds, {
      paddingTopLeft: [this.state.filtersWidth, 51],  // filters width and header height
    });
  },

  locationPin: function(featureData, latlng, layerComponent) {
    var size = 16;
    return L.marker(latlng, {
      icon: L.divIcon({
        className: 'project-location-marker',
        iconSize: [size, size],
        iconAnchor: [size/2, size/2],
      }),
    });
  },

  locationPopup: function(location) {
    var d = location.properties;
    return (
      React.createElement(Popup, null, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, titleCase(d.name))
        ), 
        React.createElement("div", {className: "panel-body location-summary"}, 
          React.createElement("p", null, 
            React.createElement("strong", null, "Project summary: "), " Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
          ), 
          React.createElement("p", {className: "details"}, 
            React.createElement("span", null, 
              React.createElement("span", {className: "locations-count"}, location.geometry.coordinates.length - 1), " other locations.", ' '
            ), 
            React.createElement(Link, {to: "project", params: {projectId: location.id}}, 
             React.createElement(NotYet, null, "See project details")
            )
          )
        )
      )
    );
  },

  filtersWidthChange: function(newWidth) {
    this.setState({filtersWidth: newWidth});
  },

  getPopupPadding: function() {
    return {
      topLeft: [this.state.filtersWidth + 20, 0],
    };
  },

  getProjectSites: function() {
    return this.country.properties.projects
      .filter(function(project) {
        return this.state.projects.length &&
          this.state.projects.indexOf(project) > -1;
      }, this)
      .map(function(project) {
        return  React.createElement(PointLayer, {
          key: project.id, 
          getMap: this.props.getMap, 
          pin: this.locationPin, 
          popup: this.locationPopup, 
          getPopupPadding: this.getPopupPadding, 
          geojson: project});
      }, this);
  },

  render: function() {
    var hasLoaded = Boolean(this.country);  // false if this.country is undefined

    var numProjectLocs = hasLoaded ?
      this.country.properties.projects.reduce(function(num, project) {
        return num + project.geometry.coordinates.length;
      }, 0) : '...';

    var header =(
      React.createElement(Header, null, 
        React.createElement(Header.Main, null, 
          React.createElement(Header.Item, null, 
            React.createElement(Link, {to: "international", className: "back"}, 
              "Global view"
            )
          ), 
          React.createElement(Header.Item, null, 
            React.createElement("a", {className: "country"}, 
              hasLoaded ? this.country.properties.name : '(loading country...)', 
              hasLoaded && (' – ' + numProjectLocs + ' Project Locations')
            )
          )
        ), 
        React.createElement(Header.Aux, null, 
          React.createElement(Header.Item, {active: true}, 
            React.createElement(NotYet, null, "Thematic Layer")
          ), 
          React.createElement(Header.Item, null, 
            React.createElement(NotYet, null, "Thematic Layer")
          ), 
          React.createElement(Header.Item, null, 
            React.createElement(NotYet, null, "Thematic Layer")
          )
        )
      )
    );

    if (!hasLoaded) {
      MetaActions.setTitle('(loading country...)');
      return header;
    }

    return (
      React.createElement("div", null, 
        header, 
        this.getProjectSites(), 
        React.createElement(Filters, {notifyWidthChange: this.filtersWidthChange})
      )
    );
  }

});


module.exports = Country;
