'use strict';

var Reflux = require('reflux');
var CountryStore = require('./countryStore');


var ProjectsStore = Reflux.createStore({

  init: function() {
    this.data = this.getInitialState();
    this.listenTo(CountryStore, this.updateCountries);
  },

  updateCountries: function(updatedCountries) {
    this.data = updatedCountries.features.reduce(function(projects, country) {
      return projects.concat(country.properties.projects || []);
    }, []);
    this.emit();
  },

  emit: function() {
    this.trigger(this.data);
  },

  getInitialState: function() {
    return [];
  },

});


module.exports = ProjectsStore;
