'use strict';

var Reflux = require('reflux');
var ProjectsStore = require('./projectsStore');
var FilterStore = require('./filterStore');


var FilteredProjectsStore = Reflux.createStore({

  init: function() {
    this.data = [];
    this._filter = null;
    this.listenTo(ProjectsStore, this.updateProjects);
    this.listenTo(FilterStore, this.updateFilter, this.initFilter);
  },

  updateProjects: function(newProjects) {
    this.data = newProjects;
    this.emit();
  },

  updateFilter: function(newFilter) {
    this.setFilter(newFilter);
  },

  initFilter: function(initialFilter) {
    this.setFilter(initialFilter, true);
  },

  setFilter: function(newFilter, quiet) {
    this._filter = newFilter;
    !quiet && this.emit();
  },

  get: function() {
    var this_ = this,
        filteredProjects = this.data;

    if (this.data.some(function(project) { return Boolean(project.properties); }) &&
        this._filter.sectors &&
        this._filter.sectors.length > 0) {
      var projectsMatchingSector = this.data.filter(function(project) {
        return project.properties.sectors.some(function(sector) {
          return this_._filter.sectors.indexOf(sector) > -1;
        });
      });
      filteredProjects = filteredProjects.filter(function(project) {
        return projectsMatchingSector.indexOf(project) > -1;
      });
    }

    return filteredProjects;
  },

  emit: function() {
    this.trigger(this.get());
  },

  getInitialState: function() {
    return this.get();
  },

});


module.exports = FilteredProjectsStore;
