'use strict';

var assign = require('object-assign');
var Reflux = require('reflux');
var CountryStore = require('./countryStore');
var Set_ = require('../util').Set_;


var sectorStore = Reflux.createStore({

  init: function() {
    this.data = new Set_([]);
    this.listenTo(CountryStore, this.updateListFromCountries);
  },

  updateListFromCountries: function(countries) {
    var allSectors = countries.features
      .filter(function(country) {
        return country.properties.hasOwnProperty('projects');
      })
      .reduce(function(acc, country) {
        return acc.concat(country.properties.projects.reduce(function(pacc, project) {
          return pacc.concat(project.properties.sectors);
        }, []));
      }, []);
    this.data.update(allSectors);
    this.emit();
  },

  emit: function() {
    this.trigger(this.data.asArray());
  },

  getInitialState: function() {
    return this.data.asArray();
  },

});


module.exports = sectorStore;
